<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="unpublish-modal"
    :close-on-click-modal="false"
    title="下架"
    :visible.sync="visible"
    @close="handleResetForm"
    width="800px"
    top="5vh"
  >
    <n-form
      class="unpublish-form"
      ref="dataForm"
      :model="form"
      :rules="rules"
      @submit.native.prevent
      label-width="100px"
      v-loading="loading"
    >
      <n-form-item label="应用名称">
        <span>{{ appInfo.name }}</span>
      </n-form-item>
      <n-form-item label="应用分类">
        <span>{{ appInfo.categoryCode }}</span>
      </n-form-item>
      <n-form-item label="应用图标">
        <img
          class="app-icon"
          :src="
            getImageUrl(appInfo.icon) || $getImgPrefix('/application/app.png')
          "
        />
      </n-form-item>
      <n-form-item label="版本">
        <span>{{ versionText }}</span>
      </n-form-item>
      <n-form-item label="下架记录" prop="note">
        <el-input
          type="textarea"
          v-model="form.note"
          placeholder="请输入更新记录"
          :autosize="{ minRows: 3 }"
        />
      </n-form-item>
    </n-form>

    <div slot="footer" class="dialog-footer">
      <el-button :loading="loading" type="primary" @click="handlePublishSubmit"
        >确 认
      </el-button>
      <el-button :loading="loading" @click="close">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
  //func
  import { getImageUrl } from '@/utils'
  import { getValidators, required } from '@/utils/validateUtils'

  import API from '@/api/application'

  export default {
    props: {
      id: {
        type: Number,
        required: true,
      },
      versionText: {
        type: String,
        required: true,
      },
    },
    data() {
      return {
        getImageUrl,
        visible: false,
        loading: false,
        appInfo: {
          categoryCode: '',
          name: '',
          icon: '',
        },
        form: {
          note: '',
        },
        rules: {
          note: getValidators([
            {
              message: '请输入下架说明',
              include: required,
              required: true,
              trigger: 'change',
            },
          ]),
        },
      }
    },
    methods: {
      getInfo() {
        this.loading = true
        this.$http({
          url: API.appInfoDetail,
          data: { id: this.id },
        })
          .then((res) => {
            const { categoryCode, name, icon } = res.data
            this.appInfo = {
              categoryCode,
              name,
              icon,
            }
          })
          .finally(() => {
            this.loading = false
          })
      },
      handlePublishSubmit() {
        this.$refs.dataForm.validate((valid) => {
          if (valid) {
            const params = {
              ...this.form,
              appId: this.id,
            }
            this.loading = true
            this.$http({
              url: API.appInfoUnpublish,
              data: params,
            })
              .then((res) => {
                if (res.msgCode == 200) {
                  this.close()
                  this.$emit('success')
                  this.$message.success('下架成功')
                }
              })
              .finally(() => {
                this.loading = false
              })
          }
        })
      },
      handleResetForm() {
        this.$refs.dataForm.resetFields()
      },
      show() {
        if (this.id) {
          this.getInfo()
        }
        this.visible = true
      },
      close() {
        this.visible = false
      },
    },

    computed: {},

    watch: {},
  }
</script>
<style lang="less" scoped>
  .unpublish-modal {
    /deep/ .el-dialog {
    }
    .unpublish-form {
      .form-input {
        width: 200px;
      }
      .form-textarea {
        width: 500px;
      }
      .app-icon {
        width: 80px;
        height: 80px;
        border-radius: 4px;
      }
    }
  }
</style>
