<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="share-page"
    :close-on-click-modal="false"
    title="页面分享"
    :visible.sync="visible"
    @close="close"
    width="800px"
    top="5vh"
    v-element-screen
  >
    <div v-loading="loading">
      <div class="share-container">
        <div class="share-header">
          <div class="info-item" style="margin-left: 20px">
            <span class="label label-title">应用版本</span>
            <span class="val">{{ info.version }}</span>
          </div>
          <div class="info-item">
            <span class="label label-title">应用状态</span>
            <span class="val">已上架</span>
          </div>
          <div class="info-item" style="flex: 100px">
            <span class="label label-title">上架时间</span>
            <span class="val">{{
              $format('yyyy年MM月dd日 hh:mm:ss', info.publishTime)
            }}</span>
          </div>
          <div class="info-item">
            <span class="label label-title">上架者</span>
            <span class="val">{{ info.publishUserAlias }}</span>
          </div>
        </div>
        <div class="label-title" style="margin-top: 30px">分享链接</div>
        <el-table class="table" :data="data" border>
          <el-table-column label="页面" width="180">
            <template slot-scope="scoped">
              <span>{{ scoped.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="状态" width="70">
            <template slot-scope="scoped">
              <el-switch
                v-model="scoped.row.shared"
                @change="switchChange(scoped.row, $event)"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column label="面包屑" width="70">
            <template slot-scope="scoped">
              <el-switch
                v-model="scoped.row.settings.showBreadcrumb" 
                @change="breadcrumbChange(scoped.row, $event)"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column label="地址">
            <template slot-scope="scoped">
              <div class="link-column">
                <el-input
                  class="link-input"
                  size="mini"
                  v-model="scoped.row.path"
                  disabled
                >
                  <el-button
                    class="copy-btn"
                    slot="append"
                    icon="el-icon-document-copy"
                    @click="copyText(scoped.row.path)"
                  >
                  </el-button>
                </el-input>
                <el-button
                  size="mini"
                  type="default"
                  style="margin-left: 10px"
                  @click="openTab(scoped.row.link)"
                  >访问</el-button
                >
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button :loading="loading" @click="close">关闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
//func
import { copyText } from '@/utils'

import API from '@/api/application'
import { instancePathPrefix, previewRoutePath } from '@/config/code-var'

//components

export default {
  components: {},
  props: {
    id: {
      type: Number,
      required: true,
    },
    // 是否已发布
    isPublish: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      visible: false,
      loading: false,
      info: {},
      data: [],
    }
  },
  mounted() {},
  methods: {
    switchChange(row, value) {
      const { appId, code } = row
      this.$http({
        url: value ? API.pageSharingSave : API.pageSharingRemove,
        data: {
          appId,
          pageCode: code,
        },
      })
    },
    breadcrumbChange(row, value) {
      const { appId, code } = row
      this.$http({
        url: API.pageSharingUpdate,
        data: {
          appId,
          pageCode: code,
          settings: JSON.stringify({
            showBreadcrumb: value
          })
        },
      })
    },
    getInfo() {
      this.loading = true
      this.$http({
        url: API.pageSharingPageList,
        data: {
          appId: this.id,
        },
      })
        .then((res) => {
          this.data = res.data.map((item, index) => {
            const { appId, code } = item
            item.path = `${instancePathPrefix}${appId}_${code}_${index}`
            item.link = `/front${previewRoutePath}?id=${appId}&code=${code}`
            item.settings = item.settings ? JSON.parse(item.settings) : {}
            return item
          })
          this.$http({
            url: API.pageSharingAppInfo,
            data: {
              appId: this.id,
            },
          }).then((res) => {
            this.info = res.data
          })
        })
        .catch((err) => {
          this.data = []
        })
        .finally(() => {
          this.loading = false
        })
    },
    open() {
      if (this.isPublish) {
        this.visible = true
        this.getInfo()
      } else {
        const h = this.$createElement
        this.$msgbox({
          title: '提示',
          message: h('div', { class: 'no-public' }, [
            h('i', { class: 'el-icon-warning tips-icon' }),
            h('div', { style: 'color: teal' }, [
              h('span', { class: 'title' }, '应用未上架'),
              h('span', { class: 'desc' }, '应用需上架后才能进行页面分享'),
            ]),
          ]),
          showCancelButton: true,
          showConfirmButton: false,
          cancelButtonText: '关闭',
        })
      }
    },
    close() {
      this.visible = false
    },
    copyText(str) {
      copyText(str)
    },
    openTab(link) {
      window.open(link)
    },
  },

  computed: {},

  watch: {},
}
</script>
