<template>
  <div class="app-detail-page" v-loading="loading">
    <header class="header">
      <div class="app-info">
        <div class="app-icon">
          <img
            :src="
              getImageUrl(info.icon) || $getImgPrefix('/application/app.png')
            "
          />
        </div>
        <div class="app-content">
          <div class="app-name">{{ info.name }}</div>
          <div class="app-content-bottom">
            <div class="app-tag">{{ info.category }}</div>
          </div>
          <div class="app-content-bottom">
            <div class="app-desc">{{ info.description }}</div>
          </div>
        </div>
        <div class="app-button-group">
          <el-button @click="goToAppList"
            ><i class="el-icon-arrow-left"></i>返回</el-button
          >
          <el-button type="primary" @click="handleOpenDesignPage"
            >可视化编辑</el-button
          >
          <el-button type="primary" @click="preview">预览</el-button>
          <el-dropdown @command="handleCommand" style='margin-right: 30px;'>
            <el-button class="more-button">
              更多<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                class="drop-down-item"
                v-for="btn in moreButtons"
                :key="btn.key"
                :command="btn.key"
                >{{ btn.title }}
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </header>
    <main class="main">
      <div class="version-list-title">版本记录</div>
      <div class="version-list">
        <div
          class="version-item"
          v-for="versionItem in versionList"
          :key="versionItem.id"
        >
          <div class="version-content">
            <div class="left">
              <div class="version-name">
                {{ versionItem.version }}
              </div>
              <div class="publish-name">
                {{ versionItem.publishAppName }}
              </div>
              <div
                :class="{
                  'version-status': true,
                  'version-is-publish': versionItem.status == 2,
                  'version-is-unpublish': versionItem.status == 3,
                }"
              >
                {{ VERSION_STATUS[versionItem.status] }}
              </div>
            </div>
            <div class="center">
              <div class="info-row">
                <div class="info-col">
                  <div class="label">创建于：</div>
                  <div class="value">
                    {{
                      versionItem.createdTime
                        ? $format('yyyy/MM/dd', versionItem.createdTime)
                        : '-'
                    }}
                  </div>
                </div>
                <div class="info-col">
                  <div class="label">上架于：</div>
                  <div class="value">
                    {{
                      versionItem.publishTime
                        ? $format('yyyy/MM/dd', versionItem.publishTime)
                        : '-'
                    }}
                  </div>
                </div>
                <div class="info-col">
                  <div class="label">下架于：</div>
                  <div class="value">
                    {{
                      versionItem.unpublishTime
                        ? $format('yyyy/MM/dd', versionItem.unpublishTime)
                        : '-'
                    }}
                  </div>
                </div>
              </div>
              <div class="info-row">
                <div class="info-col">
                  <div class="label">创建者：</div>
                  <div class="value">
                    {{ versionItem.createdUserAlias || '-' }}
                  </div>
                </div>
                <div class="info-col">
                  <div class="label">上架者：</div>
                  <div class="value">
                    {{ versionItem.publishUserAlias || '-' }}
                  </div>
                </div>
                <div class="info-col">
                  <div class="label">下架者：</div>
                  <div class="value">
                    {{ versionItem.unpublishUserAlias || '-' }}
                  </div>
                </div>
              </div>
            </div>
            <div class="right">
              <div class="app-button-group">
                <el-button
                  v-if="versionItem.status !== 3"
                  type="primary"
                  size="mini"
                  @click="handleOnOrOffShelf(versionItem)"
                  >{{ versionItem.status === 2 ? '下架' : '上架' }}</el-button
                >
                <el-button
                  v-if="versionItem.status != 1"
                  type="primary"
                  size="mini"
                  @click="
                    versionItem.showVersionDesc = !versionItem.showVersionDesc
                  "
                  >版本说明
                  <i
                    :class="{
                      'el-icon--right': true,
                      'el-icon-arrow-down': !versionItem.showVersionDesc,
                      'el-icon-arrow-up': versionItem.showVersionDesc,
                    }"
                  ></i>
                </el-button>
                <el-dropdown
                  @command="(key) => handleVersionCommand(key, versionItem)"
                >
                  <el-button class="more-button" size="mini">
                    更多<i class="el-icon-arrow-down el-icon--right"></i>
                  </el-button>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item
                      class="drop-down-item"
                      v-for="btn in versionMoreButtons"
                      :key="btn.title"
                      :command="btn.key"
                      >{{ btn.title }}
                    </el-dropdown-item>
                    <el-dropdown-item
                      v-show="versionItem.status == 1"
                      class="drop-down-item"
                      key="删除"
                      command="delete"
                      >删除
                    </el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </div>
            </div>
          </div>
          <div class="version-desc" v-show="versionItem.showVersionDesc">
            <div class="publish-desc">
              <div class="title">版本说明：</div>
              <div class="value">{{ versionItem.publishNote || '-' }}</div>
            </div>
            <div
              class="unpublish-desc"
              v-show="versionItem.unpublishNote"
            >
              <div class="title">下架说明：</div>
              <div class="value">{{ versionItem.unpublishNote || '-' }}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- <div class="pagination">
                <pagination
                    :page="queryForm.pageIndex"
                    :limit="queryForm.pageSize"
                    :total="total"
                    @pagination="handlePageChange"
                />
            </div> -->
    </main>
    <create-app
      :id="id"
      ref="createApp"
      @success="createAppConfirm"
    ></create-app>
    <share-page
      :id="id"
      ref="sharePage"
      :isPublish="isPublish"
    ></share-page>
    <publish-modal
      :id="id"
      :versionId="versionId"
      :versionText="versionText"
      ref="publishModal"
      @success="getVersionList"
    />
    <unpublish-modal
      :id="id"
      :versionText="versionText"
      ref="unpublishModal"
      @success="getVersionList"
    />
    <SourceManage ref="sourceManage" />
    <el-dialog class="app-export" title="应用导出" :visible="exportDialogVisible">
      <el-form label-width="120px" label-position="left">
        <el-form-item label="导出模式" style="margin-bottom: 0;">
          <el-row>
            <el-radio-group v-model="exportMode" size="mini">
              <el-radio-button label="CREATION">默认模式</el-radio-button>
              <el-radio-button label="COVER">全量模式</el-radio-button>
            </el-radio-group>
          </el-row>
          <el-row class="export-mode-tips" v-html="exportTips[exportMode]"></el-row>
        </el-form-item>
        <el-form-item label="导出数据源" v-if="exportMode === 'CREATION'">
          <el-radio :value="exportDataSource" :label="true" @click.prevent.native="exportDataSource = !exportDataSource">导出该应用中使用的数据源配置和调用信息</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="exportDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="exportApp">导 出</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  //url
  import API from '@/api/application.js'

  // func
  import { exportFile, getImageUrl, loadCustomIconfont } from '@/utils'
  import { jumpPreview } from '@/utils/url'
  import KEY from '@/store/base-info/mutation-types'

  //components
  import Pagination from '@/components/Pagination/index.vue'
  import SourceManage from '@/components/SourceManage/index.vue'
  import PublishModal from './components/publishModal'
  import UnpublishModal from './components/unpublishModal'
  import CreateApp from '../components/createApp'
  import SharePage from '../components/sharePage'

  import { authKey } from '@/router'
  import { store } from '@project/frame'

  const VERSION_STATUS = {
    1: '未上架',
    2: '已上架',
    3: '已下架',
  }
  export default {
    name: 'AppDetail',
    components: {
      Pagination,
      CreateApp,
      PublishModal,
      UnpublishModal,
      SharePage,
      SourceManage,
    },
    data() {
      return {
        getImageUrl,
        VERSION_STATUS,
        id: this.$route.query.id,
        versionId: 0,
        versionText: '',
        loading: false,
        info: {},
        versionList: [],
        tableLoading: false,
        queryForm: {
          pageIndex: 1,
          pageSize: 100,
        },
        total: 0,
        moreButtons: [
          {
            title: '基础配置',
            key: 'edit',
          },
          {
            title: '资源管理',
            key: 'source',
          },
          {
            title: '页面分享',
            key: 'share',
          },
          {
            title: '应用导出',
            key: 'export',
          },
          {
            title: '删除',
            key: 'delete',
          },
        ],
        // 版本列表 更多按鈕
        versionMoreButtons: [
          {
            title: '预览',
            key: 'preview',
          },
          // {
          //   title: '资源管理',
          //   key: 'source',
          // },
          {
            title: '编辑',
            key: 'edit',
          },
          {
            title: '复制',
            key: 'copy',
          },
          // {
          //   title: '生成模板',
          //   key: 'createMod',
          // },
        ],
        exportDialogVisible: false,
        exportMode: 'CREATION',
        exportDataSource: true,
        exportTips: {
          CREATION: '默认导出应用的所有版本信息和配置内容，但不包括版本的上下架状态；\n导入结果为新增一个全新应用',
          COVER: '全量导出应用的所有版本信息、版本状态、配置内容和数据源信息；\n导入结果为覆盖更新已存在的应用版本，或者导入一个全新应用',
        }
      }
    },
    methods: {
      preview() {
        jumpPreview({
          appVersionId: this.info.latestAppDesignId,
          appId: this.info.id
        })
      },
      // 创建app成功回调
      createAppConfirm() {
        this.getInfo()
      },
      // 查询应用信息详情
      getInfo() {
        this.loading = true
        this.$http({
          url: API.appInfoDetail,
          data: { id: this.id },
        })
          .then((res) => {
            this.info = res.data
          })
          .finally(() => {
            this.loading = false
          })
      },
      // 获取版本信息
      getVersionList() {
        this.$http({
          url: API.appDesignList,
          data: { appId: this.id },
        }).then((res) => {
          let { data } = res
          data.some((item) => {
            item.showVersionDesc = false
          })
          this.versionList = data
          this.isPublish = data.some(item => item.status === 2)
        })
      },
      handleCommand(key) {
        switch (key) {
          case 'edit':
            this.$refs.createApp.show()
            break
          case 'share':
            this.$refs.sharePage.open()
            break
          case 'export':
            this.exportDialogVisible = true
            break
          case 'delete':
            this.handleDeleteApp()
            break
          case 'source':
            this.$refs.sourceManage.open()
            break
          default:
            console.log('key is in the default')
            break
        }
      },
      async handleDeleteApp() {
        const { name, id } = this.info
        await this.$confirm(`确认删除${name}吗？`)
        this.loading = true
        this.$http({
          url: API.appInfoRemove,
          data: { id },
        })
          .then(() => {
            this.$message.success('删除成功')
            this.goToAppList()
          })
          .finally(() => {
            this.loading = false
          })
      },
      goToAppList() {
        if (history.length > 2) {
          rootApp.$router.back()
        } else {
          rootApp.$router.replace({ name: authKey.Application })
        }
      },
      handlePageChange({ page, size }) {
        this.queryForm.pageIndex = page
        this.queryForm.pageSize = size
      },
      async handleOpenDesignPage() {
        const data = this.getVersionData({ data: this.versionList[0] })
        await store.dispatch(KEY.VERSION_JUDGE, { data, reload: false, createCallBack: this.createVersionCallback })
        rootApp.$router.push({
          name: authKey.AppDesign,
          query: {
            id: this.info.id,
            vid: data.id,
            appId: this.info.id
          },
        })
      },
      createVersionCallback() {
        this.$message.success('新建成功!')
        this.getInfo()
        this.getVersionList()
      },
      getVersionData({ vid, data }) {
        if(!vid && !data) {
          console.error('数据源为空')
          return {}
        }
        const { id, status, version } = data || this.versionList.find(item => item.id == vid)
        return {
          status,
          appId: this.id,
          id,
          version
        }
      },
      // 上架/下架
      handleOnOrOffShelf(versionInfo) {
        switch (versionInfo.status) {
          case 1:
          case 3:
            {
              const hasPublic = this.versionList.some(item => item.status === 2)
              if(hasPublic) {
                return this.$message.error('需先下架已上线的应用版本')
              }
              // 打开上架弹窗
              this.versionId = versionInfo.id
              this.versionText = versionInfo.version
              this.$refs.publishModal.show()
              break
            }
          case 2:
            // 打开下架弹窗
            this.versionText = versionInfo.version
            this.$refs.unpublishModal.show()
            break
        }
      },

      // 版本列表更多按钮回调
      handleVersionCommand(key, versionInfo) {
        switch (key) {
          case 'preview':
            this.handlePreviewByVersion(versionInfo)
            break
          case 'edit':
            this.handleEditAppByVersion(versionInfo)
            break
          case 'copy':
            {
              const data = this.getVersionData({ data: versionInfo })
              store.dispatch(KEY.VERSION_JUDGE, { data, message: '输入新的版本号', reload: false, isCopy: true, createCallBack: this.createVersionCallback })
            }
            break
          case 'createMod':
            this.handleCreateModByVersion(versionInfo)
            break
          case 'delete':
            this.handleDeleteAppByVersion(versionInfo)
            break
          case 'source':
            this.$refs.sourceManage.open()
            break
          default:
            console.log('key is in the default')
            break
        }
      },

      // 预览指定版本
      handlePreviewByVersion(versionInfo) {
        console.log(versionInfo);
        jumpPreview({
          appVersionId: versionInfo.id,
          appId: this.$route.query.id
        })
      },

      // 编辑指定版本
      async handleEditAppByVersion(info) {
        const data = this.getVersionData({ data: info })
        await store.dispatch(KEY.VERSION_JUDGE, { data, reload: false, createCallBack: this.createVersionCallback })
        rootApp.$router.push({
          name: authKey.AppDesign,
          query: {
            id: this.info.id,
            vid: info.id,
            appId: this.info.id
          },
        })
      },

      // 根据指定版本生成模板
      handleCreateModByVersion(versionInfo) {},

      //删除指定版本
      async handleDeleteAppByVersion(versionInfo) {
        const { version, id } = versionInfo
        await this.$confirm(`确认删除版本${version}吗？`)
        this.$http({
          url: API.appDesignRemove,
          data: { id },
        }).then(() => {
          this.getInfo()
          this.getVersionList()
        })
      },
      // 应用导出
      exportApp() {
        exportFile({
          url: API.appInfoExport,
          data: {
            params: {
              appId: this.info.id,
              exportDataSource: this.exportDataSource,
              operation: this.exportMode
            },
          },
        })
      },
    },
    created() {
      loadCustomIconfont()
      this.getInfo()
      this.getVersionList()
    },
  }
</script>

