<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="publish-modal"
    :close-on-click-modal="false"
    title="上架"
    :visible.sync="visible"
    @close="handleResetForm"
    width="800px"
    top="5vh"
  >
    <n-form
      class="publish-form"
      ref="dataForm"
      :model="form"
      :rules="rules"
      @submit.native.prevent
      label-width="100px"
      v-loading="loading"
    >
      <n-form-item label="应用名称" prop="name">
        <el-input v-model="form.name" placeholder="输入应用名称"></el-input>
      </n-form-item>
      <n-form-item label="应用分类">
        <span>{{ appInfo.categoryCode }}</span>
      </n-form-item>
      <n-form-item label="应用图标">
        <img
          class="app-icon"
          :src="
            getImageUrl(appInfo.icon) || $getImgPrefix('/application/app.png')
          "
        />
      </n-form-item>
      <n-form-item label="应用配图">
        <div class="gray-tip-text">
          上传2-5张应用代表性图片（尺寸保持一致），尺寸不小于x*y像素，支持格式为jpg、png、jpeg、gif、bmp等，单张图片大小不能超过1M
        </div>
        <div class="screenshot-container">
          <div class="screenshot-list">
            <div
              class="screenshot-item"
              v-for="(item, index) in form.screenshot"
              :key="index"
            >
              <img :src="getImageUrl(item)" />
              <i
                class="el-icon-circle-cross"
                @click="handleDeleteScreenshotByIndex(index)"
              />
            </div>
          </div>
          <Upload
            v-if="form.screenshot.length < 5"
            class="app-icon"
            ref="upload"
            @change="handleUploadSuccess"
          />
        </div>
      </n-form-item>
      <n-form-item label="版本">
        <span>{{ versionText }}</span>
      </n-form-item>
      <n-form-item label="更新记录" prop="note">
        <el-input
          type="textarea"
          v-model="form.note"
          placeholder="请输入更新记录"
          :autosize="{ minRows: 3 }"
        />
      </n-form-item>
    </n-form>

    <div slot="footer" class="dialog-footer">
      <el-button :loading="loading" type="primary" @click="handlePublishSubmit"
        >确 认
      </el-button>
      <el-button :loading="loading" @click="close">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
  //func
  import { getImageUrl } from '@/utils'
  import { getValidators, required } from '@/utils/validateUtils'

  import API from '@/api/application'

  //components
  import Upload from '@/components/Upload'

  export default {
    components: {
      Upload,
    },
    props: {
      id: {
        type: Number,
        required: true,
      },
      versionId: {
        type: Number,
        required: true,
      },
      versionText: {
        type: String,
        required: true,
      },
    },
    data() {
      let validateScreenshot = (rule, value, callback) => {
        if (!value.length) {
          callback(new Error('请上传应用配图'))
        } else if (value.length <= 1 || value.length > 5) {
          callback(new Error('请上传2-5张应用配图'))
        } else {
          callback()
        }
      }
      return {
        getImageUrl,
        visible: false,
        loading: false,
        appInfo: {
          categoryCode: '',
          name: '',
          icon: '',
        },
        form: {
          screenshot: [],
          note: '',
          name: ''
        },
        rules: {
          name: getValidators([
            {
              message: '请输入上架名称',
              include: required,
              required: true,
              trigger: 'change',
            },
          ]),
          screenshot: {
            validator: validateScreenshot,
            trigger: 'blur',
            required: false
          },
          note: getValidators([
            {
              message: '请输入上架说明',
              include: required,
              required: true,
              trigger: 'change',
            },
          ]),
        },
      }
    },
    methods: {
      getInfo() {
        this.loading = true
        this.$http({
          url: API.appInfoDetail,
          data: { id: this.id },
        })
          .then((res) => {
            const { categoryCode, name, icon } = res.data
            this.appInfo = {
              categoryCode,
              name,
              icon,
            }
            this.form.name = name
          })
          .finally(() => {
            this.loading = false
          })
      },
      // 上传应用配图成功回调
      handleUploadSuccess(v) {
        this.form.screenshot.push(v)
      },
      // 删除应用配图
      handleDeleteScreenshotByIndex(index) {
        this.form.screenshot.splice(index, 1)
      },
      handlePublishSubmit() {
        this.$refs.dataForm.validate((valid) => {
          if (valid) {
            const { name: appName, ...others } = this.form
            const params = {
              ...others,
              appId: this.id,
              appDesignId: this.versionId,
              appName
            }
            this.loading = true
            this.$http({
              url: API.appInfoPublish,
              data: params,
            })
              .then((res) => {
                if (res.msgCode == 200) {
                  this.close()
                  this.$emit('success')
                  this.$message.success('上架成功')
                }
              })
              .finally(() => {
                this.loading = false
              })
          }
        })
      },
      handleResetForm() {
        this.$refs.dataForm.resetFields()
      },
      show() {
        if (this.id) {
          this.getInfo()
        }
        this.visible = true
      },
      close() {
        this.visible = false
      },
    },

    computed: {},

    watch: {},
  }
</script>
